.App {
    display: flex;
    padding: 10px;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-content: center;
}

.appText {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    animation: pulse 5s infinite;
}

@keyframes pulse {
    0% {
        opacity: 0%;
    }
    50% {
        opacity: 100%;
    }
    100% {
        opacity: 0%;
    }
}